import { mapState } from 'vuex'
import { formatMoney } from '@/util'
import audio from '@/audio'
import ProgressBar from '@/components/Progress'
export default {
  components: {
    ProgressBar
  },
  props: {
    pot: Object
  },
  data () {
    return {
      inGrowUp: false,
      value: 0
    }
  },
  methods: {
    // 
    onPlantpotMouseDown () {
      if (this.pot.plant) return // 
      if (this.user.money - this.currPlant.cost >= 0) {
        this.$store.commit('setPlant', this.pot)
      }
      let plant = this.pot.plant
      this.inGrowUp = true
      // 
      let speed = plant.speed
      // 
      audio.playSe('grow', this.seVolume / 100)
      // 
      this.value = 0
      let delay = 2
      if (this.effects === 1) {
        delay = 5
      } else if (this.effects === 2) {
        delay = 20
      } else if (this.effects === 3) {
        delay = 100
      }
      this.timer = setInterval(() => {
        this.value += 100 / delay
        // 
        if (this.value >= 100) {
          // 
          clearInterval(this.timer)
          this.inGrowUp = false
          // 
          this.popMoney(plant.profit)
          // 
          this.$store.commit('getPlant', this.pot)
          // 
          this.value = 0
          // 
          // this.$emit('done', this.pot)
        }
      }, speed / delay)
    },
    // 
    popMoney (money) {
      let popDom = document.createElement('div')// dom
      popDom.classList.add('pop-money')// domclass
      if (this.effects > 0) {
        popDom.classList.add('effects')
      }
      popDom.innerHTML = `${formatMoney(money)}`
      this.$refs.paper.appendChild(popDom)// ref="paper"dom
      setTimeout(() => {
        popDom.remove()
      }, 500)
    }
  },
  computed: {
    ...mapState(['user', 'seVolume', 'currPlant', 'effects']),
    postLevelGolden: function () {
      return {
        golden: this.pot.level === 2
      }
    }
  }
}



