import Startup from '@/views/Startup/Startup'
import User from '@/views/info/User'
import Garden from '@/views/garden/Garden'
import PlantPicker from '@/views/PlantPicker'
import Menus from '@/views/info/Menus'
export default {
  components: {
    Startup,
    User,
    Garden,
    PlantPicker,
    Menus
  },
  data () {
    return {
    }
  },
  mounted () {
    this.init()// 
  },
  methods: {
    init () {
      // 
      this.$store.commit('changePlant', '')
      this.$nextTick(() => {
        console.log('1')
        this.$store.commit('load')
        console.log('2')
      })
      this.loopSeason()
    },
    loopSeason () {
      this.saveTimer = setInterval(() => {
        this.$store.commit('save')
        console.log('')
      }, 1000 * 30)
    }
  }
}



